.footer {
    min-height: 20vh;
    background-color: #a6dca6;
}

.footerContainer {
display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.footerBlock {
    width: 280px;
    border: 1px solid;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.footerBlockContent {
    background-color: darkgrey;
    width: 60px;
    height: 60px;
}



